/* GLOBAL */
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* BLOCKS */
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #333333;
}
.main-header {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding-left: 40px;
    padding-right: 40px;
  }
}
.main-header__logo {
  margin-right: auto;
  padding-top: 12px;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin-right: 0;
    padding-top: 22px;
  }
}
.main-container {
  flex-grow: 1;
}
@media (min-width: 1440px) {
  .main-footer {
    padding-top: 44px;
  }
}
@media (min-width: 768px) {
  .main-footer__container {
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .main-footer__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}
@media (min-width: 768px) {
  .main-footer__content {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }
}
.main-footer__col {
  padding: 20px 0;
}
@media (min-width: 768px) {
  .main-footer__col {
    width: 222px;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .main-footer__col {
    width: 318px;
  }
}
.main-footer__col--contacts {
  padding-bottom: 7px;
}
.main-footer hr {
  position: relative;
  left: -20px;
  height: 1px;
  width: calc(100% + 40px);
  box-sizing: border-box;
  margin: 0;
  border-width: 0;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
