    display: none;
  }
}
.main-footer__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-right: 0;
  padding-bottom: 20px;
  padding-left: 0;
}
@media (min-width: 768px) {
  .main-footer__header {
    padding-top: 42px;
    padding-bottom: 30px;
  }
}
@media (min-width: 1440px) {
  .main-footer__header {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 0;
    padding-bottom: 0;
  }
}
.main-footer__row {
  padding-top: 20px;
  padding-right: 20px;
  padding-bottom: 30px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .main-footer__row {
    padding-top: 30px;
    padding-bottom: 40px;
  }
}
@media (min-width: 1440px) {
  .main-footer__row {
    padding-top: 20px;
  }
}
@media (min-width: 1440px) {
  .main-footer__logo {
    margin-bottom: 50px;
  }
}
.main-footer__title {
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  color: #7859cf;
}
.main-footer__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
.main-footer__item {
  display: flex;
  align-items: center;
}
.main-footer__item:not(:last-child) {
  margin-bottom: 4px;
}
.main-footer__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #333333;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  width: 260px;
  padding-top: 8px;
  padding-right: 0;
  padding-bottom: 8px;
  padding-left: 0;
}
.main-footer__link:hover,
.main-footer__link:focus-visible {
  color: #9070ec;
}
.main-footer__link:active {
  color: #593cac;
}
@media (min-width: 1440px) {
  .main-footer__link {
    width: 318px;
  }
}
.container {
  width: calc(100% - 40px);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .container {
    width: 728px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1360px;
  }
}
.main-nav {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
}
.main-nav__toggle {
  position: relative;
  height: 60px;
  width: 60px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  right: 22px;
  width: 16px;
  height: 1.5px;
  box-shadow: 0 1.5px 0 0 #333333, 0 6.75px 0 0 #333333, 0 12px 0 0 #333333;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 23px;
  height: 1.5px;
  width: 15px;
  background-color: #333333;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav__list {
    width: 100%;
    box-sizing: border-box;
    position: absolute;
    top: 60px;
    left: 0;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
    position: relative;
    top: 0;
    left: 0;
    order: 1;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  z-index: 2;
  list-style-type: none;
  border-top: 1px solid #f0ebff;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    flex-wrap: wrap;
    border-top: none;
    max-width: 480px;
  }
}
@media (min-width: 1440px) {
  .site-list {
    flex-direction: row;
    column-gap: 20px;
    border-top: none;
  }
}
.site-list__link {
  display: block;
  width: 100%;
  text-align: center;
  padding: 20px 0 21px;
  border-bottom: 1px solid #f0ebff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  color: #333333;
}
@media (min-width: 768px) {
  .site-list__link {
    border-bottom: 0;
    width: auto;
    max-width: 155px;
    padding: 32px 20px;
  }
  .site-list__link--current {
    position: relative;
    color: #7859cf;
  }
  .site-list__link--current::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #7859cf;
  }
}
.site-list__link:hover,
.site-list__link:focus-visible {
  color: #7859cf;
}
.site-list__link:active:not(.site-list__link--current) {
  background-color: #f0ebff;
}
.user-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  max-width: 180px;
  list-style-type: none;
}
@media (min-width: 1440px) {
