  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .user-list__link {
    padding: 32px 22px;
  }
}
.user-list__link-text {
  display: none;
  margin-left: 8px;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  text-transform: uppercase;
