.slider .swiper-button-prev,
.slider .swiper-button-next {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 20px;
  height: 100%;
  top: 22px;
  background-color: transparent;
  cursor: pointer;
}
.slider .swiper-button-prev::after,
.slider .swiper-button-next::after {
