}
.slider .swiper-button-next {
  right: 0;
}
.slider .swiper-button-next:hover,
.slider .swiper-button-next:focus-visible {
  background: linear-gradient(to left, #ffffff, rgba(255, 255, 255, 0));
}
.slider .swiper-button-next:active {
