.radio-control {
  position: relative;
  display: flex;
  align-items: center;
  height: 24px;
  padding-left: 34px;
}
.radio-control__icon {
  position: absolute;
  box-sizing: border-box;
  height: 24px;
  width: 24px;
  top: 0;
  left: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 50%;
  border-color: #bdbdbd;
  background-color: #ffffff;
}
.radio-control__icon::before {
  content: "";
  position: absolute;
  display: none;
  width: 16px;
  height: 16px;
  top: 2.5px;
  left: 2.5px;
  border-radius: 50%;
}
.radio-control__text {
  font-size: 16px;
  line-height: 24px;
}
/* Hover */
.radio-control:hover .radio-control__icon {
  border-color: #7859cf;
}
.radio-control:hover .radio-control__icon::before {
  display: block;
  background-color: #7859cf;
  opacity: 0.25;
}
/* Focus */
.radio-control input[type="radio"]:focus-visible ~ .radio-control__icon {
  border-color: #7859cf;
}
.radio-control input[type="radio"]:focus-visible ~ .radio-control__icon::before {
  display: block;
  background-color: #7859cf;
  border-radius: 50%;
  opacity: 0.25;
}
/* Checked */
.radio-control input[type="radio"]:checked + .radio-control__icon {
  border-color: #7859cf;
}
.radio-control input[type="radio"]:checked + .radio-control__icon::before {
  display: block;
  background-color: #7859cf;
  border-radius: 50%;
}
/* Disabled */
.radio-control input[type="radio"]:disabled ~ .radio-control__text {
  opacity: 0.5;
}
.radio-control input[type="radio"]:disabled ~ .radio-control__icon {
  opacity: 0.5;
  border-color: #bdbdbd;
}
.radio-control input[type="radio"]:disabled ~ .radio-control__icon::before {
  background-color: #bdbdbd;
}
.checkbox-control {
  position: relative;
  display: flex;
  align-items: center;
  height: 24px;
  padding-left: 52px;
}
.checkbox-control__icon {
  position: absolute;
  box-sizing: border-box;
  height: 24px;
  width: 42px;
  top: 0;
  left: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 12px;
  border-color: #bdbdbd;
  background-color: #bdbdbd;
}
.checkbox-control__icon::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0;
  top: 0;
  background-color: #ffffff;
  border-radius: 50%;
}
/* Hover */
.checkbox-control:hover .checkbox-control__icon {
  border-color: #7859cf;
  background-color: #7859cf40;
}
/* Focus */
.checkbox-control input[type="checkbox"]:focus-visible ~ .checkbox-control__icon {
  border-color: #7859cf;
  background-color: #7859cf40;
}
/* Checked */
.checkbox-control input[type="checkbox"]:checked + .checkbox-control__icon {
  border-color: #7859cf;
  background-color: #7859cf;
}
.checkbox-control input[type="checkbox"]:checked + .checkbox-control__icon::before {
  left: auto;
  right: 0;
}
/* Disabled */
.checkbox-control input[type="checkbox"]:disabled ~ .checkbox-control__text {
  opacity: 0.5;
}
.checkbox-control input[type="checkbox"]:disabled ~ .checkbox-control__icon {
  background-color: #bdbdbd80;
  border-color: transparent;
}
.checkbox-control input[type="checkbox"]:checked:disabled ~ .checkbox-control__icon::before {
  background-color: #bdbdbd;
}
